body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace
}

.visit-counter {
  background-color: #f8f9fa;
  border: 1px solid #e9ecef;
  font-family: Arial, sans-serif
}

.visit-counter p {
  color: #495057;
  font-size: .9em;
  margin: 0
}

.visit-counter {
  background-color: #f0f0f0;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin: 20px 0;
  padding: 10px
}

.visit-counter p {
  color: #333;
  font-size: 1em;
  margin: 5px 0
}

body {
  font-size: 14px;
  font-size: 16px
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh
}

h1 {
  font-size: 2rem
}

h2 {
  font-size: 1.5rem
}

.main-content {
  flex: 1 1;
  flex-direction: row;
  flex-wrap: wrap
}

.left-column, .right-column {
  flex: 1 1;
  min-width: 300px;
  padding: 20px
}

@media (max-width:768px) {
  .main-content {
    flex-direction: column
  }

  .left-column, .right-column {
    width: 100%
  }
}

:root {
  --primary-color: #2c3e50;
  --secondary-color: #3498db;
  --background-color: #f8f9fa;
  --text-color: #333;
  --light-gray: #e9ecef
}

body {
  background-color: #f8f9fa;
  background-color: var(--background-color);
  color: #333;
  color: var(--text-color);
  font-family: Inter, sans-serif;
  line-height: 1.6;
  margin: 0;
  padding: 0
}

.App {
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 20px
}

a, li, p, span {
  font-size: 1em;
  line-height: 1.6
}

h1, h2, h3, h4, h5, h6 {
  color: #2c3e50;
  color: var(--primary-color);
  margin-top: 0
}

h1 {
  font-size: 2em;
  margin-bottom: 20px
}

h2 {
  font-size: 1.5em;
  margin-bottom: 15px
}

h3 {
  font-size: 1.2em;
  margin-bottom: 10px
}

h4 {
  font-size: 1.1em;
  margin-bottom: 5px
}

ol, ul {
  margin-bottom: 20px;
  padding-left: 20px
}

.education-list, .experience-list, .projects-list, .skills-list {
  list-style-type: none;
  padding-left: 0
}

.education-item, .experience-item, .project-item {
  margin-bottom: 15px
}

@media (max-width:768px) {
  body {
    font-size: 14px
  }

  .App {
    padding: 0 10px
  }
}

nav {
  background-color: #fff;
  box-shadow: 0 2px 5px #0000001a;
  padding: 10px 0;
  position: sticky;
  top: 0;
  z-index: 1000
}

nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style-type: none;
  margin: 0;
  padding: 0
}

nav ul li {
  margin: 0 10px
}

nav ul li a {
  color: #2c3e50;
  color: var(--primary-color);
  font-size: .9em;
  font-weight: 500;
  padding: 5px 0;
  text-decoration: none;
  transition: color .3s ease
}

nav ul li a:hover {
  color: #3498db;
  color: var(--secondary-color)
}

.main-content {
  display: flex;
  gap: 40px;
  margin: 0 auto;
  max-width: 1200px;
  padding: 20px
}

.left-column {
  flex: 1 1;
  max-width: 300px
}

.right-column {
  flex: 2 1
}

.contact-info, .header-content {
  margin-bottom: 30px
}

#about {
  margin-bottom: 40px
}

#about h2 {
  color: #2c3e50;
  color: var(--primary-color);
  font-size: 1.8em;
  margin-bottom: 15px
}

#about p {
  line-height: 1.6
}

.profile-photo {
  border: 3px solid #2c3e50 !important;
  border: 3px solid var(--primary-color) !important;
  border-radius: 50% !important;
  box-shadow: 0 4px 8px #0000001a !important;
  height: 180px !important;
  object-fit: cover !important;
  width: 180px !important
}

.header-content {
  margin-bottom: 30px
}

@media (max-width:768px) {
  .profile-photo {
    height: 200px;
    width: 200px
  }
}

h1 {
  font-size: 2.5em;
  margin-bottom: 10px
}

.subtitle {
  font-size: 1.2em;
  margin-bottom: 20px
}

.contact-info {
  margin-top: 20px
}

.contact-info p {
  align-items: center;
  display: flex;
  margin-bottom: 10px
}

.contact-info i {
  color: #3498db;
  color: var(--secondary-color);
  margin-right: 10px
}

h2 {
  align-items: center;
  color: #2c3e50;
  color: var(--primary-color);
  display: flex;
  margin-bottom: 20px
}

h2:before {
  content: attr(data-icon);
  font-size: 1.2em;
  margin-right: 10px
}

section {
  margin-bottom: 40px;
  scroll-margin-top: 60px
}

ul {
  padding-left: 20px
}

.skill-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  padding: 0
}

.skill-item {
  border-radius: 15px;
  font-size: .9em;
  padding: 5px 10px
}

a {
  color: #3498db;
  color: var(--secondary-color);
  text-decoration: none;
  transition: color .3s ease
}

a:hover {
  color: #2c3e50;
  color: var(--primary-color)
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px)
  }

  to {
    opacity: 1;
    transform: translateY(0)
  }
}

@media (max-width:768px) {
  .main-content {
    flex-direction: column
  }

  .left-column, .right-column {
    padding: 0
  }

  .profile-photo {
    height: 150px;
    width: 150px
  }

  h1 {
    font-size: 2em
  }

  h2 {
    font-size: 1.5em
  }
}

.highlight {
  background-color: #e9ecef;
  background-color: var(--light-gray);
  border-radius: 10px;
  margin-bottom: 30px;
  padding: 20px
}

.highlight-items {
  align-items: center;
  display: flex;
  justify-content: space-around
}

.highlight-item {
  padding: 10px;
  text-align: center
}

.highlight-item i {
  color: #3498db;
  color: var(--secondary-color);
  font-size: 1.5em;
  margin-bottom: 5px
}

.highlight-item p {
  font-size: .9em;
  margin: 0
}

.highlight-item strong {
  color: #2c3e50;
  color: var(--primary-color);
  font-size: 1.1em
}

@media (max-width:768px) {
  .highlight-items {
    flex-direction: column
  }

  .highlight-item {
    margin-bottom: 15px
  }
}

.projects-grid {
  grid-gap: 20px;
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr))
}

.project-card {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 5px #0000001a;
  transition: transform .3s ease
}

.project-card:hover {
  transform: translateY(-5px)
}

.project-card h3 {
  color: #2c3e50;
  color: var(--primary-color);
  margin-top: 0
}

html {
  scroll-behavior: smooth
}

@media (max-width:768px) {
  .highlight-items {
    flex-direction: column
  }

  .highlight-item {
    margin-bottom: 20px
  }

  .projects-grid {
    grid-template-columns: 1fr
  }
}

.visit-counter {
  margin-top: 20px;
  text-align: center
}

skills-container {
  display: flex;
  flex-wrap: wrap;
  margin: -5px
}

.skill-item {
  background-color: #e9ecef;
  background-color: var(--light-gray);
  border-radius: 25px;
  color: #2c3e50;
  color: var(--primary-color);
  display: inline-block;
  font-size: 1em;
  margin: 5px;
  padding: 10px 20px;
  transition: background-color .3s ease, transform .3s ease
}

.skill-item:hover {
  background-color: #3498db;
  background-color: var(--secondary-color);
  color: #fff;
  transform: translateY(-2px)
}

footer {
  border-top: 1px solid #e9ecef;
  border-top: 1px solid var(--light-gray);
  margin-top: 40px;
  padding: 20px 0;
  text-align: center
}

.visit-counter {
  color: #3498db;
  color: var(--secondary-color);
  font-size: .9em
}

.visit-counter i {
  margin-right: 5px
}

.header-content {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px
}

.profile-photo {
  border-radius: 50%;
  box-shadow: 0 0 10px #0000001a;
  height: 120px;
  margin-bottom: 15px;
  object-fit: cover;
  width: 120px
}

.header-text {
  text-align: center
}

h1 {
  color: #2c3e50;
  color: var(--primary-color);
  font-size: 2.2em;
  margin: 0
}

.subtitle {
  color: #3498db;
  color: var(--secondary-color);
  font-size: 1.1em;
  margin: 3px 0 0
}

@media (max-width:768px) {
  .header-content {
    flex-direction: column;
    text-align: center
  }

  .profile-photo {
    margin-bottom: 15px
  }
}

.awards-list, .documents-list {
  list-style-type: none;
  padding: 0
}

.award-item, .document-item {
  margin-bottom: 20px
}

.award-item h3, .document-item h3 {
  color: #2c3e50;
  color: var(--primary-color);
  margin-bottom: 5px
}

.document-item a {
  background-color: #3498db;
  background-color: var(--secondary-color);
  border-radius: 5px;
  color: #fff;
  display: inline-block;
  margin-top: 5px;
  padding: 5px 10px;
  text-decoration: none;
  transition: background-color .3s ease
}

.document-item a:hover {
  background-color: #2c3e50;
  background-color: var(--primary-color)
}

.education-list {
  list-style-type: none;
  padding: 0
}

.education-item {
  margin-bottom: 30px
}

.education-item h3 {
  color: #2c3e50;
  color: var(--primary-color);
  margin-bottom: 5px
}

.education-item p {
  margin: 5px 0
}

.thesis-info {
  margin-top: 10px
}

.thesis-info p {
  font-style: italic;
  margin-bottom: 5px
}

.thesis-info a {
  background-color: #3498db;
  background-color: var(--secondary-color);
  border-radius: 5px;
  color: #fff;
  display: inline-block;
  font-size: .9em;
  padding: 5px 10px;
  text-decoration: none;
  transition: background-color .3s ease
}

.thesis-info a:hover {
  background-color: #2c3e50;
  background-color: var(--primary-color)
}

.header-nav {
  background-color: #e9ecef;
  background-color: var(--light-gray);
  box-shadow: 0 2px 5px #0000001a;
  padding: 10px 0;
  position: sticky;
  top: 0;
  z-index: 1000
}

.header-nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style-type: none;
  margin: 0;
  padding: 0
}

.header-nav ul li {
  margin: 0 10px
}

.header-nav ul li a {
  color: #2c3e50;
  color: var(--primary-color);
  font-size: .9em;
  font-weight: 500;
  padding: 5px 0;
  text-decoration: none;
  transition: color .3s ease
}

.header-nav ul li a:hover {
  color: #3498db;
  color: var(--secondary-color)
}

@media (max-width:768px) {
  .header-nav ul {
    align-items: center;
    flex-direction: column
  }

  .header-nav ul li {
    margin: 5px 0
  }
}

#education, #experience {
  margin-bottom: 30px
}

#education h3, #experience h3 {
  color: #2c3e50;
  color: var(--primary-color);
  font-size: 1.2em;
  font-weight: 600;
  margin-bottom: 10px
}

.education-list, .experience-list {
  list-style-type: none;
  padding-left: 20px
}

.education-list li, .experience-list li {
  margin-bottom: 15px
}

.education-list h4, .experience-list h4 {
  color: #2c3e50;
  color: var(--primary-color);
  font-size: 1.1em;
  margin: 0 0 2px
}

.education-list p, .experience-list p {
  font-size: .9em;
  line-height: 1.3;
  margin: 0
}

.education-list p:last-child, .experience-list p:last-child {
  color: #3498db;
  color: var(--secondary-color);
  font-size: .85em
}

.experience-industry, .experience-teaching {
  margin-bottom: 20px
}

.experience-teaching {
  border-top: 2px solid #e9ecef;
  border-top: 2px solid var(--light-gray);
  padding-top: 15px
}

.education-item .thesis-info {
  font-style: italic;
  margin-top: 5px
}

.project-card {
  background-color: #e9ecef;
  background-color: var(--light-gray);
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 20px;
  position: relative
}

.github-link {
  color: #2c3e50;
  color: var(--primary-color);
  font-size: 24px;
  position: absolute;
  right: 10px;
  top: 10px;
  transition: color .3s ease
}

.github-link:hover {
  color: #3498db;
  color: var(--secondary-color)
}

.scholar-link {
  align-items: center;
  color: #2c3e50;
  color: var(--primary-color);
  display: inline-flex;
  margin-left: 10px;
  text-decoration: none;
  transition: color .3s ease
}

.scholar-link:hover {
  color: #3498db;
  color: var(--secondary-color)
}

.scholar-link i {
  font-size: 1.2em;
  margin-right: 5px
}

.visit-counter-wrapper {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  /* Ajuste conforme necessário */
}

.visit-counter-wrapper img {
  display: block;
  /* Isso remove qualquer espaço extra abaixo da imagem */
}